<script>
    import fastapi from "../lib/api"
    import { link } from 'svelte-spa-router'
    import { page, is_login } from "../lib/store"
    
    import moment from 'moment/min/moment-with-locales'

    moment.locale('ko')

    let question_list = []
    let size = 10
    let total = 0
    $: total_page = Math.ceil(total/size)  //   $:      , 
                                           // total   API      total_page     

    function get_question_list(_page) {
        let params ={
            page: _page,
            size: size,
        }
        fastapi('get', '/api/question/list', params, (json) => {
            question_list = json.question_list
            $page = _page
            total = json.total
        })
    }

    $: get_question_list($page)    //   $:  $page     
</script>

<div class="container my-3">
    <table class="table">
        <thead>
        <tr class="text-center table-dark">
            <th></th>
            <th style="width:50%"> </th>
            <th></th>
            <th></th>
            <th></th>
        </tr>
        </thead>
        <tbody>
        {#each question_list as question, i}
        <tr class="text-center">
            <td>{ total - ( $page * size ) - i }</td>
            <td class="text-start">
                <a use:link href="/detail/{question.id}">{question.subject}</a>
            </td>
            <td>{moment(question.create_date).format("YYYY MM DD")}</td>  <!-- : hh:mm a : : / -->
            <td>{ question.user ? question.user.username : "" }</td>
        </tr>
        {/each}
        </tbody>
    </table>

    <!--   -->
    <ul class="pagination justify-content-center">
        <!--  -->
        <li class="page-item {$page <= 0 && 'disabled'}">
            <button class="page-link" on:click="{() => get_question_list($page-1)}"></button>
        </li>
        <!--  -->
        {#each Array(total_page) as _, loop_page}
        {#if loop_page >= $page-5 && loop_page <= $page+5} 
        <li class="page-item {loop_page === $page && 'active'}">
            <button on:click="{() => get_question_list(loop_page)}" class="page-link">{loop_page+1}</button>
        </li>
        {/if}
        {/each}
        <!--  -->
        <li class="page-item {$page >= total_page-1 && 'disabled'}">
            <button class="page-link" on:click="{() => get_question_list($page+1)}"></button>
        </li>
    </ul>
    <!--   -->

    <a use:link href="/question-create" 
        class="btn btn-primary {$is_login ? '' : 'disabled'}">  </a>
</div>